.topContainer {
  width: 100%;
  height: 730px;
  background: url(../../images/page/background/HomePageBG.png) no-repeat;
  background-size: 100%;
}

.navbar {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: fixed;
  top: 0;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 2;
}

.navbar .text {
  width: 75%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "FuturicaRegular";
}

.navbar .text a {
  text-decoration: none;
  color: white;
}

.navbar .text p {
  display: flex;
  align-items: center;
  color: white;
  font-size: 18px;
  text-decoration: none !important;
  color: white;
}

.navbar .text .login {
  background-color: transparent;
  border: none;
  padding-left: 10px;
  padding-right: 10px;
  cursor: pointer;
  font-family: "FuturicaRegular";
}

.navbar .text .login img {
  margin-right: 14.5px;
}

.navbar .text .login:focus {
  outline: none;
}

.navbar .text .putAd {
  width: 132px;
  height: 41px;
  background-color: #5ABA42;
  color: white;
  letter-spacing: 1px;
  font-size: 11px;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 121px;
  font-family: "ComfortaaBold";
}

.navbar .text .putAd span {
  margin-right: 5px;
  font-size: 24px;
}

.navbar .text .putAd:focus {
  outline: none;
}
